import React, { Component } from "react";
import { connect } from "react-redux";
import { compose } from "redux";

import CustomerTypeCreateForm from "./createForm";

import notification from "../../../../components/notification";
// import { Input, Select, Divider } from "antd";

import LayoutWrapper from "../../../../components/utility/layoutWrapper";
import PageHeader from "../../../../components/utility/pageHeader";
import IntlMessages from "../../../../components/utility/intlMessages";
import Button from "../../../../components/uielements/button";
import actions from "../../../../redux/customer/customerType/actions";
import { Box } from "../../../App/app.style";

const redirectTo = "/dashboard/setup/customer-types";


class CreateCustomerType extends Component {
  handleSubmit = value => {
    const { storeCustomerType } = this.props;

    // Uppercasing customerType code
    // value.customer_type_code = value.customer_type_code.toUpperCase();
    // console.log(value);
    storeCustomerType(value);
  };

  componentDidUpdate() {
    // Display error notif
    if (this.props.customerType.error) {
      notification("error", this.props.customerType.error);
      this.props.clearError();
    }

    if (this.props.customerType.success) {
      this.props.history.replace(redirectTo)
    }
  }

  render() {
    // console.log(this.props);
    return (
      <LayoutWrapper>
        <Box>
          <PageHeader title={<IntlMessages id="customerTypes.add.title" />}
            style={{ margin: 0 }}
          >
            <Button onClick={() => this.props.history.replace(redirectTo)}>
              <IntlMessages id="button.back" />
            </Button>
          </PageHeader>

          <CustomerTypeCreateForm
            auth={this.props.auth}
            submit={this.handleSubmit}
            customerType={this.props.customerType}
          />
        </Box>
      </LayoutWrapper>
    );
  }
}

const mapStateToProps = state => {
  return {
    auth: state.auth, customerType: state.customerType
  };
};

export default compose(
  connect(
    mapStateToProps,
    actions
  )
)(CreateCustomerType);
